.side-project-card {
  display: flex;
  flex-direction: row;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 0 48px 0 rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  background-color: rgb(52, 81, 94);
}

.side-project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .side-project-card {
    flex-direction: column;
    padding: 1rem;
  }
}

.side-project-image {
  flex: 1;
  margin-right: 20px;
}

.side-project-image img {
  width: 100%;
  border-radius: 5px;
}

.side-project-details {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.side-project-title {
  margin-bottom: 20px;
}

.side-project-description {
  margin-bottom: 20px;
}

.side-project-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

@media (max-width: 768px) {
  .side-project-card {
    flex-direction: column;
  }

  .side-project-image {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
