.social-media-div {
  font-size: 2em;
}

.icon-button {
  margin-bottom: 10px;
}

/*noinspection CssUnresolvedCustomProperty*/
.icon-wrapper {
  border-radius: 2.6rem;
  cursor: pointer;
  display: inline-block;
  font-size: 1.6rem;
  height: 2.6rem;
  line-height: 2.6rem;
  position: relative;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 2.6rem;
  margin: 0 5px 10px;
  background-color: var(--background-color);
  transition: background-color 0.3s ease-in;
}

/*noinspection CssUnresolvedCustomProperty*/
.icon-wrapper:hover {
  background-color: var(--hover-background-color);
}

.icon-wrapper svg {
  color: white;
}

/* Media Query */
@media (max-width: 768px) {
  .social-media-div {
    text-align: center;
  }
}
