:root {
  --primary-font: "Google Sans Regular", sans-serif;
  --heading-font: "Google Sans Medium", sans-serif;
  --primary-color: #000;
  --secondary-color: #868e96;
  --card-shadow: 4px 3px 20px -2px grey;
}

.experience-main {
  width: 100%;
}

.basic-experience {
  margin: 0 5%;
}

.experience-heading-div {
  display: flex;
}

.experience-heading-div > * {
  flex: 1;
}

.experience-heading-img-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.experience-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: var(--heading-font);
  line-height: 1.1;
  text-align: center;
  margin-top: 80px;
}

.experience-heading-sub-text {
  font-size: 30px;
  font-family: var(--primary-font);
  text-align: center;
  margin-bottom: 10px;
}

.experience-header-detail-text {
  font-size: 20px;
  line-height: 30px;
  margin: 0 20px;
  font-family: var(--heading-font);
}

@media (max-width: 768px) {
  .experience-heading-text,
  .experience-heading-sub-text {
    font-size: 20px;
  }

  .experience-heading-div {
    flex-direction: column;
  }

  .experience-header-detail-text {
    font-size: 16px;
    margin-right: 0;
    line-height: normal;
    text-align: center;
  }

  .experience-card,
  .experience-card-logo {
    flex-direction: column;
    text-align: center;
  }

  .arrow-left,
  .experience-card-stepper {
    display: none;
  }

  .experience-list-item {
    flex-direction: column;
  }

  .experience-card-heading-left,
  .experience-card-heading-right {
    margin: auto;
  }

  .experience-card-header-div {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .experience-card-description {
    margin: 0 14px;
    text-align: justify;
  }
}

.experience-card {
  min-width: 350px;
  margin-bottom: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin-left: 10px;
  box-shadow: var(--card-shadow);
}

.arrow-left {
  width: 0;
  height: 0;
  margin: 40px 10px -10px;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  z-index: 100;
}

.experience-card-logo-div {
  margin: -10px 10px 10px;
}

.experience-card-stepper {
  display: flex;
  position: relative;
  align-items: center;
  flex-direction: column;
  margin-top: -40px;
}

.experience-card-header-div {
  display: flex;
  flex-direction: row;
}

.experience-card-logo {
  width: 100px;
  height: 100px;
  box-shadow: var(--card-shadow);
  border-radius: 50px;
}

.experience-card-title,
.experience-card-company,
.experience-card-duration,
.experience-card-location,
.experience-card-description {
  font-family: var(--primary-font);
  margin: 0;
}

.experience-card-title {
  margin: 10px 7px 2px 0;
  text-align: left;
}

.experience-card-duration {
  margin: 10px 0 2px;
  text-align: right;
  color: var(--secondary-color);
}

.experience-card-heading-left {
  float: left;
  margin-left: 10px;
  margin-right: auto;
}

.experience-card-heading-right {
  float: right;
  margin-left: auto;
  margin-right: 10px;
}

.experience-card-location {
  text-align: right;
  color: var(--secondary-color);
}

.experience-card-description {
  margin: 5px 10px 0 10px;
  text-align: justify;
}

.experience-card-company > a {
  color: var(--primary-color);
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s ease-in-out;
}

.experience-card-company > a:hover {
  border-color: var(--primary-color);
}

.experience-list-item {
  display: flex;
  align-items: center;
  flex-direction: row;
}
