/* Base styles for main containers */
.main,
.greet-main,
.greeting-main {
  width: 90%;
  padding: 20px 10px;
  margin: auto;
}

.greet-main {
  margin-top: 2rem;
}

.main {
  margin-top: 4rem;
}

.greeting-main {
  display: flex;
  flex-wrap: wrap; /* Ensures flexibility */
}

.greeting-main > *,
.button-greeting-div {
  flex: 1;
  margin-bottom: 30px;
}

.button-greeting-div {
  display: flex;
  margin-top: 20px;
  justify-content: flex-start; /* Default alignment */
}

/* Text styles */
.greeting-text,
.greeting-nickname,
.greeting-text-p {
  font-family: "Google Sans", sans-serif; /* Fallback to sans-serif */
  line-height: 1.1;
  margin-top: 80px;
}

.greeting-text {
  font-size: 70px;
}

.greeting-nickname {
  font-size: 30px;
  line-height: 0;
  font-style: italic;
}

.greeting-text-p {
  font-size: 40px;
  line-height: 40px;
  margin-right: 40px;
}

.greeting-image-div > * {
  max-width: 100%;
  height: auto;
}

/* Media Queries */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .button-greeting-div {
    justify-content: space-around;
  }
  .greeting-text {
    font-size: 30px;
    margin-top: 0;
    margin-right: 0;
    line-height: normal;
    text-align: center;
  }
  .greeting-nickname {
    font-size: 25px;
    margin-top: 0;
    margin-right: 0;
    line-height: normal;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    margin-top: 0;
    margin-right: 0;
    line-height: normal;
    text-align: center;
  }
  .greeting-main {
    display: block;
  }
  .portfolio-repo-btn-div {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
